import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import { berhasil, gagal } from "../Alert";
import FormSaldoAwalPiutang from "./Component/FormSaldoAwalPiutang";
import { getListDataSaldoAwalPiutangCustomer } from "../actions/transaksuPenjualanLMAction";
import { Link } from "react-router-dom";
import HeaderCard from "../HeaderCard";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { AxiosMasterPost } from "../Axios";
import { finishSend, progressSend } from "../actions/LogicAction";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    dataSaldoAwalPiutangCustomer: state.provinsi.dataSaldoAwalPiutangCustomer,
    customer: state.provinsi.customer,
    sales: state.provinsi.salesman,
    onSend: state.provinsi.onSend,
  };
};
class SaldoAwalPiutangCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tipeTransksi: "HUTANG",
      listSales: [],
      tanggal: "",
      columns: [
        {
          dataField: "kode_customer",
          text: "Kode Toko",
        },
        {
          dataField: "kode_sales",
          text: "Kode Sales",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "bruto",
          text: "Bruto",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "netto",
          text: "Netto",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "saldo_gr",
          text: "Saldo GR",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "saldo_rp",
          text: "Saldo RP",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "titip_gr",
          text: "Titip rp",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "titip_rp",
          text: "Titip RP",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, index) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => this.hapus(index)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  hapus(index) {
    let hasil = JSON.parse(localStorage.getItem("SaldoAwalPiutangCustomer"));
    hasil.splice(index, 1);
    localStorage.setItem("SaldoAwalPiutangCustomer", JSON.stringify(hasil));
    this.props.dispatch(getListDataSaldoAwalPiutangCustomer());
  }
  sendSaldoAwal() {
    if (this.state.tanggal === "") {
      gagal("Mohon isi tanggal");
      return false;
    } else if (this.state.sales === []) {
      gagal("Mohon isi sales");
      return false;
    } else if (localStorage.getItem("SaldoAwalPiutangCustomer") === null) {
      gagal("Data masih Kosong, Silahkan tambahkan dahulu");
      return false;
    }
    this.props.dispatch(progressSend());
    let data = {
      no_transaksi: this.props.noFaktur,
      jenis_trx: "PIUTANG",
      tanggal: this.state.tanggal,
      kode_lokasi: "-",
      detail_saldo_awal: JSON.parse(
        localStorage.getItem("SaldoAwalPiutangCustomer")
      ),
    };

    AxiosMasterPost("saldo-awal/simpan", data)
      .then(() => berhasil("Saldo Awal Berhasil Disimpan"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => localStorage.removeItem("SaldoAwalPiutangCustomer"))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(getListDataSaldoAwalPiutangCustomer()))
      .catch((err) =>
        gagal(`Gagal Simpan Saldo Awal, Error : ${err.response.data}`).then(
          () => this.props.dispatch(finishSend())
        )
      );
  }

  setTipe(e) {
    this.setState({
      tipeTransksi: e.target.value,
    });
    this.props.dispatch(reset("FormSaldoAwalPiutang"));
  }

  componentDidMount() {
    this.props.dispatch(getUUID());

    this.props.dispatch(getListDataSaldoAwalPiutangCustomer());
  }

  handleChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handlerSimpan(data) {
    var isi = {
      kode_supplier: "-",
      kode_customer: data.kode_customer,
      kode_sales: data.kode_sales,
      no_bon: data.no_bon,
      saldo_gr: data.saldo_gr,
      saldo_rp: data.saldo_rp,
      titip_gr: data.titip_gr,
      titip_rp: data.titip_rp,
      kode_jenis: "-",
      kadar_cetak: "0",
      bruto: data.bruto,
      netto: data.netto,
    };
    console.log(JSON.stringify(isi));
    var hasil =
      JSON.parse(localStorage.getItem("SaldoAwalPiutangCustomer")) || [];
    hasil.push(isi);
    localStorage.setItem("SaldoAwalPiutangCustomer", JSON.stringify(hasil));
    berhasil("Data Tersimpan");
    this.props.dispatch(getListDataSaldoAwalPiutangCustomer());
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/saldoawal">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Saldo Awal Piutang Customer" />
              <div className="card-body">
                <div className="col-lg-12 mt-3">
                  <div className="row">
                    <div className="col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        className="form-control"
                        onChange={(e) =>
                          this.setState({
                            tanggal: e.target.value,
                          })
                        }
                      />
                    </div>

                    <div className="col-lg-12 mt-3">
                      <div className="row">
                        <div className="col-lg-6"></div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              data-target="#tambahBarang"
                              data-toggle="modal"
                            >
                              Tambah Data
                            </button>
                          </div>
                        </div>
                        <div className="col-lg-12 mt-3">
                          <GlobalTabel
                            keyField="id"
                            data={this.props.dataSaldoAwalPiutangCustomer}
                            columns={this.state.columns}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          type="submit"
                          className="btn btn-info"
                          disabled={this.props.onSend}
                          onClick={() => this.sendSaldoAwal()}
                        >
                          {this.props.onSend ? (
                            <span
                              className="spinner-border spinner-border-sm mr-3"
                              aria-hidden="true"
                            ></span>
                          ) : null}
                          Simpan
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="tambahBarang"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-full-width">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Transaksi
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormSaldoAwalPiutang
                  onSubmit={(data) => this.handlerSimpan(data)}
                  tipe={this.state.tipeTransksi}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

SaldoAwalPiutangCustomer = reduxForm({
  form: "SaldoAwalPiutangCustomer",
  enableReinitialize: true,
})(SaldoAwalPiutangCustomer);
export default connect(maptostate, null)(SaldoAwalPiutangCustomer);
