import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validasiSaldoAwalPiutang from "../../Validations/validasiSaldoAwalPiutang";
import { renderField, renderFieldSelect } from "../../renderField";
import { getcustomerList } from "../../actions/customerAction";
import { getsalesmanList } from "../../actions/salesmanAction";

class FormSaldoAwalPiutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getsalesmanList());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="kode_customer"
              component={renderFieldSelect}
              options={this.props.customer.map((list) => {
                let data = {
                  value: list.kode_customer,
                  name: list.kode_customer + " - " + list.nama_customer,
                };
                return data;
              })}
              type="text"
              label="Kode CUstomer"
              placeholder="Masukan Kode CUstomer"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="kode_sales"
              component={renderFieldSelect}
              options={this.props.sales.map((list) => {
                let data = {
                  value: list.kode_sales,
                  name: list.nama_sales,
                };
                return data;
              })}
              type="text"
              label="Kode Customer"
              placeholder="Masukan Kode Customer"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Nomor Bon"
              name="no_bon"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Bruto(Gr)"
              name="bruto"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Netto(Gr)"
              name="netto"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Saldo(Gr)"
              name="saldo_gr"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="saldo(Rp)"
              name="saldo_rp"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Titip(Gr)"
              name="titip_gr"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Titip(Rp)"
              name="titip_rp"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalPiutang = reduxForm({
  form: "FormSaldoAwalPiutang",
  enableReinitialize: true,
  validate: validasiSaldoAwalPiutang,
})(FormSaldoAwalPiutang);
export default connect((state) => {
  return {
    customer: state.provinsi.customer,
    onSend: state.provinsi.onSend,
    sales: state.provinsi.salesman,
  };
})(FormSaldoAwalPiutang);
